<template lang="pug">
  .programme
    MTitle(:mtitle="$t('content.jjfa')",subtitle="为企业提供一系列软件相关的配套服务")
    .programme-content#certify
      swiper(:options="swiperOption")
        swiper-slide
          .programme-item
            img(:src="img1")
            .programme-item-content
              .programme-item-content-title
                h3 工程解决方案
                p 以效益为中心，以质量为中心
              p(style="height: 30px")
              p.nr 系统设计引入世界先进客户关系管理系统的设计理念，借鉴其他领域CRM成熟的设计经验，结合电力行业特色，提炼出满足于中国电力行业CRM的功能需求，能够达到改善客户关系、提高客户满意度、增加企业效益的目标要求。
              a(href="javascpirt:void(0)",class="team-more",target="_blink") MORE
                i.icon.pe-angle-right-circle
        swiper-slide
          .programme-item
            img(:src="img2")
            .programme-item-content
              .programme-item-content-title
                h3 工程解决方案
                p 以效益为中心，以质量为中心
              p(style="height: 30px")
              p.nr 系统设计引入世界先进客户关系管理系统的设计理念，借鉴其他领域CRM成熟的设计经验，结合电力行业特色，提炼出满足于中国电力行业CRM的功能需求，能够达到改善客户关系、提高客户满意度、增加企业效益的目标要求。
              a(href="javascpirt:void(0)",class="team-more",target="_blink") MORE
                i.icon.pe-angle-right-circle
        swiper-slide
          .programme-item
            img(:src="img3")
            .programme-item-content
              .programme-item-content-title
                h3 工程解决方案
                p 以效益为中心，以质量为中心
              p(style="height: 30px")
              p.nr 系统设计引入世界先进客户关系管理系统的设计理念，借鉴其他领域CRM成熟的设计经验，结合电力行业特色，提炼出满足于中国电力行业CRM的功能需求，能够达到改善客户关系、提高客户满意度、增加企业效益的目标要求。
              a(href="javascpirt:void(0)",class="team-more",target="_blink") MORE
                i.icon.pe-angle-right-circle
</template>

<script>
  import MTitle from '../../components/MTitle'
  import img1 from '../../assets/img/1520910295.jpg'
  import img2 from '../../assets/img/1520910256.jpg'
  import img3 from '../../assets/img/1520910850.jpg'
  export default {
    name: "programme",
    data () {
      return {
        swiperOption: {
          watchSlidesProgress: true,
          centeredSlides: true,
          loop: true,
          loopedSlides: 5,
          slidesPerView: 'auto',
          autoplay: true,
          on: {
            progress: function(progress) {
              for (let i = 0; i < this.slides.length; i++) {
                var slide = this.slides.eq(i);
                var slideProgress = this.slides[i].progress;
                // console.log(slideProgress, 'progress')
                let modify = 1;
                // Math.abs()  返回绝对值
                if (Math.abs(slideProgress) > 1) {
                  modify = (Math.abs(slideProgress) - 1) * 0.4 + 1;
                }
                // console.log(modify, 'modify')
                let translate = slideProgress * modify * 321 + 'px';
                // console.log(translate)
                let scale = 1 - Math.abs(slideProgress) / 5;
                let zIndex = 999 - Math.abs(Math.round(10 * slideProgress));
                slide.transform('translateX(' + translate + ') scale(' + scale + ')');
                slide.css('zIndex', zIndex);
                slide.css('opacity', 1);
                if (Math.abs(slideProgress) > 3) {
                  slide.css('opacity', 0);
                }
              }
            },
            setTransition: function(transition) {
              for (let i = 0; i < this.slides.length; i++) {
                var slide = this.slides.eq(i)
                slide.transition(transition);
              }
            }
          }
        },
        img1: img1,
        img2: img2,
        img3: img3
      }
    },
    methods: {
    },
    components: {
      MTitle
    }
  }
</script>

<style lang="less">
  .programme{
    height: auto;
    width: 100%;
    text-align: center;
    padding: 50px 0;
    padding-top: 60px;
    background-color: #ddd;
  }
  .programme-item{
    width: 100%;
    height: auto;
    .programme-item-content-title{
      line-height: 30px;
      margin: 0;
      color: #808080;
      font-size: 16px;
      h3{
        color: #00c9a1;
        font-size: 24px;
        margin-top: 20px;
        margin-bottom: 10px;
        font-weight: 400;
      }
      p{
        color: #a6cfc8;
      }
    }
    p{
      line-height: 20px;
    }
    .nr{
      color: #999999;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      overflow: hidden;
      line-height: 20px;
      padding: 0 30px;
    }
    a{
      display: block;
      color: #63857f;
      font-size: 15px;
      transition: color .25s;
      margin-top: 30px;
      &:hover{
        color: #00c9a1;
      }
    }
  }
  #certify {
    position: relative;
    width: 1200px;/*判断宽度设置响应式效果*/
    margin: 60px auto
  }

  #certify .swiper-container {
    padding-bottom: 60px;
  }

  #certify  .swiper-slide {
    width: 642px;
    height: 684px;
    background: #fff;
    border:1px solid #c2c2c2;
  }
  #certify  .swiper-slide img{
    width: 638px;
    height: 440px;
    display:block;
    border:2px solid #fff;
  }

  #certify .swiper-pagination {
    width: 100%;
    bottom: 20px;
  }

  #certify .swiper-pagination-bullets .swiper-pagination-bullet {
    margin: 0 5px;
    border: 3px solid #fff;
    background-color: #d5d5d5;
    width: 10px;
    height: 10px;
    opacity: 1;
  }

  #certify .swiper-pagination-bullets .swiper-pagination-bullet-active {
    border: 3px solid #00aadc;
    background-color: #fff;
  }

</style>



