<template lang="pug">
  .mtitle
    h2 {{ mtitle}}
    .line
    p(v-if="subtitle != ''") {{ subtitle }}
</template>

<script>
  export default {
    name: "mtitle",
    props: {
      mtitle: {
        type: String,
        default: ''
      },
      subtitle: {
        type: String,
        default: ''
      }
    },
    data () {
      return {
      }
    },
    methods: {
    }
  }
</script>

<style lang="less">
  .mtitle{
    width: 80%;
    height: auto;
    margin: 30px auto;
    position: relative;
    z-index: 5;
    h2{
      font-size: 36px;
      color: #00c9a1;
    }
    p{
      padding: 0 10%;
      margin-top: 15px;
      font-size: 16px;
      color: #999999;
    }
    .line{
      height: 1px;
      width: 160px;
      background-color: #00c9a1;
      margin: 0 auto;
      margin-top: 10px;
    }
  }
</style>



