<template lang="pug">
  .partner
    // .mask
    MTitle(:mtitle="$t('content.hzhb')",subtitle="为企业提供一系列软件相关的配套服务")
    .partner-content
      Row(:gutter="30")
        Col(:xs="12", :sm="8", :md="6", :lg="4", v-for="(item,index) in imgs", :key="index")
          .partner-item
            img(:src="item")
</template>

<script>
  import MTitle from '../../components/MTitle'

  import img from '../../assets/img/cnzx.png'
  export default {
    name: "partner",
    data () {
      return {
        imgs: [
          img,
          img,
          img,
          img,
          img,
          img,
          img,
          img,
          img,
          img,
          img,
        ]
      }
    },
    methods: {
    },
    components: {
      MTitle
    },
    mounted () {
    }
  }
</script>

<style lang="less">
  .partner{
    height: auto;
    width: 100%;
    text-align: center;
    padding: 50px 10%;
    padding-top: 60px;
    position: relative;
    .partner-content{
      width: 100%;
      height: auto;
      .ivu-col{
        margin-top: 20px;
      }
      img{
        width: 102%;
        height: 200%;
        margin-top: -1%;
        margin-left: -1%;
        transition: all .6s;
        filter: grayscale(100%);  // 灰度
        cursor: pointer;
        &:hover{
          transform: scale(1.1, 1.1);
          filter: grayscale(0);
        }
      }
    }
  }
</style>



