<template lang="pug">
  .contactus
    Row(:gutter="30")
      Col(:xs="12", :sm="6", :md="6", :lg="4", v-for="(item,index) in menuList", :key="index")
        .menulist-item
          h3 {{ item.title }}
          .item-list
            p(v-for="(menuitem,itemIndex) in item.menu", :key="itemIndex") {{ menuitem }}
      Col(:xs="24", :sm="24", :md="24", :lg="8")
        .contactus-content
          p.phone 18888888888
          p.time 周一至周六：8:30~18:00
          i.iconfont.icon-weixin1(style="font-size: 26px;color:#83b944")
          i.iconfont.icon-youxiang(style="font-size: 30px;color:#f96868")
</template>

<script>
  export default {
    name: "contactus",
    data () {
      return {
        menuList:[
          {
            title: '关于公司',
            menu: ['公司简介', '团队介绍', '使命愿景', '企业文化']
          },
          {
            title: '关于产品',
            menu: ['硬件产品', '软件产品', '物联网解决方案']
          },
          {
            title: '解决方案',
            menu: ['水泵物联网', '电机物联网', '工厂物联网', '数据分析']
          },
          {
            title: '技术支持',
            menu: ['技术说明', '下载中心']
          }
        ]
      }
    },
    methods: {
    },
    components: {
    },
    mounted () {
    }
  }
</script>

<style lang="less">
  .contactus{
    height: auto;
    width: 100%;
    text-align: center;
    padding: 50px 10%;
    padding-top: 60px;
    position: relative;
    background-color: #021c11;
    border-color: #032115;
    .menulist-item{
      text-align: center;
      color: #fff;
      height: 130px;
      h3{
        font-size: 16px;
        font-weight: 300;
      }
      .item-list{
        margin-top: 10px;
        p{
          line-height: 20px;
          color: #969696;
          cursor: pointer;
          &:hover{
            color: #00c9a1;
          }
        }
      }
    }
    .contactus-content{
      text-align: center;
      color: #fff;
      .phone{
        font-size: 26px;
        color: #00c9a1;
        cursor: pointer;
        &:hover{
          opacity: 0.6;
        }
      }
      .time{
        color: #969696;
        font-size: 14px;
      }
      i{
        line-height: 40px;
        margin: 0 20px;
        cursor: pointer;
      }
    }
  }
</style>



