<template lang="pug">
  .case
    // .mask
    MTitle(:mtitle="$t('content.al')",subtitle="为企业提供一系列软件相关的配套服务")
    .team-content
      swiper(:options="swiperOption")
        swiper-slide(v-for="(item, index) in caseData",:key="index")
          .team-item
            img(:src="item.img")
            .team-item-content
              h3 {{ item.title }}
              p {{ item.content }}
              a(href="javascript:void(0)",title="",class="more")
                span(data-more="了解更多") 了解更多
    .swiper-button-prev
      Icon(type="chevron-left")
    .swiper-button-next
      Icon(type="chevron-right")
</template>

<script>
  import MTitle from '../../components/MTitle'

  import img from '../../assets/img/1510219741.jpg'
  export default {
    name: "case",
    data () {
      return {
        swiperOption: {
          slidesPerView: 3,
          spaceBetween: 30,
          navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
          },
          pagination: {
            el: '.swiper-pagination'
          },
          breakpoints: {
            1366: {
              slidesPerView: 3,
              spaceBetween: 40
            },
            1024: {
              slidesPerView: 2,
              spaceBetween: 30
            },
            768: {
              slidesPerView: 1,
              spaceBetween: 20
            },
            640: {
              slidesPerView: 1,
              spaceBetween: 10
            }
          }
        },
        caseData: [
          {
            img: img,
            title:'小型公寓酒店',
            content: 'VR酒店全景制作，给人身临其境的感受，真切感受到房间与周围环境，置身其中。'
          },
          {
            img: img,
            title:'小型公寓酒店',
            content: 'VR酒店全景制作，给人身临其境的感受，真切感受到房间与周围环境，置身其中。'
          },
          {
            img: img,
            title:'小型公寓酒店',
            content: 'VR酒店全景制作，给人身临其境的感受，真切感受到房间与周围环境，置身其中。'
          },
          {
            img: img,
            title:'小型公寓酒店',
            content: 'VR酒店全景制作，给人身临其境的感受，真切感受到房间与周围环境，置身其中。'
          },
          {
            img: img,
            title:'小型公寓酒店',
            content: 'VR酒店全景制作，给人身临其境的感受，真切感受到房间与周围环境，置身其中。'
          },
        ]
      }
    },
    methods: {
    },
    components: {
      MTitle
    }
  }
</script>

<style lang="less">
  .case{
    height: auto;
    width: 100%;
    text-align: center;
    padding: 50px 10%;
    padding-top: 60px;
    position: relative;
    .team-content{
      margin-top: 60px;
      z-index: 5;
      .team-item{
        width: 100%;
        height: 570px;
        img{
          width: 100%;
          max-height: 400px;
          min-height: 300px;
          vertical-align: middle;
        }
        .team-item-content{
          padding: 20px;
          text-align: left;
          h3{
            margin-top: 0;
            color: #37474f;
            text-transform: capitalize;
            font-size: 24px;
          }
          p{
            color: #757575;
            max-height: 38px;
            overflow: hidden;
            margin-top: 10px;
          }
          .more{
            position: relative;
            border: 1px solid #ff6600;
            overflow: hidden;
            display: inline-block;
            height: 40px;
            line-height: 40px;
            text-align: center;
            -o-transition: all 0.26s ease 200ms;
            transition: all 0.26s ease 200ms;
            color: #fff;
            width: 160px;
            cursor: pointer;
            margin-top: 20px;
            &:after{
              content: '了解更多';
              position: absolute;
              height: 40px;
              top: 0px;
              left: 0px;
              right: 0;
              transform: translate(0,100%);
              transition: all 0.26s ease 200ms;
              background-color: #ff6600;
              color: #fff;
            }
            &:hover{
              border: 1px solid #ff6600 !important;
              transition: all 0.26s ease 200ms;
              background: #ff6600;
              text-decoration: none;
              &:after{
                top: -40px;
              }
              span{
                transform: translate(0,-100%);
              }
            }
            span{
              color: #ff6600;
              display: block;
              width: 100%;
              position: relative;
              text-align: center;
              height: 40px;
              line-height: 40px;
              transition: all 0.26s ease 100ms;
            }
          }
        }
      }
    }
  }
  .swiper-slide{
    height: auto;
    background-color: #ddd;
  }
  .swiper-container{
    z-index: 5;
  }
  .swiper-button-prev{
    margin-top: 50px;
    display: inline-block;
    width: 42px;
    border: 1px solid #131313;
    position: relative;
    left: -10px;
    height: 42px;
    cursor: pointer;
    transition: all .6s ease;
    font-size: 26px;
    line-height: 42px;
    font-weight: 300;
    color: #131313;
    background: #fff;
    &:hover{
      background: #00c9a1;
      color: #fff;
      border: 1px solid #00c9a1;
    }
  }
  .swiper-button-next{
    display: inline-block;
    width: 42px;
    border: 1px solid #131313;
    position: relative;
    right: -10px;
    margin-top: 50px;
    height: 42px;
    cursor: pointer;
    transition: all .6s ease;
    font-size: 26px;
    line-height: 42px;
    color: #131313;
    background: #fff;
    &:hover{
      background: #00c9a1;
      color: #fff;
      border: 1px solid #00c9a1;
    }
  }
</style>



