<template lang="pug">
  .ideas
    video(:src="bigimg",type="video/mp4",id="media",:autoplay="true",loop="loop",visibility="visible",style="height: 949px; width: 2320px; margin-top: 0px;margin-left: -399.3px;")
    .mask
    .container
      h1 Hello, 物联网
      h2 产品与理念
      span.line
</template>

<script>
  import bigimg from '../../assets/video/bigimg.mp4'
  export default {
    name: "ideas",
    data () {
      return {
        bigimg: bigimg
      }
    },
    methods: {
    },
    mounted () {
      document.getElementById('media').style.width = document.body.clientWidth + 400 + 'px'
      document.getElementById('media').style.height = window.screen.height - 130 + 'px'
    }
  }
</script>

<style lang="less">
  .ideas{
    height: 949px;
    width: 100%;
    overflow: hidden;
    position: relative;
    video{
      position: relative;
      z-index: -99999;
      object-fit: cover;
    }
    .mask{
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(0,0,0,0.5);
      z-index: 3;
    }
    .container{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      width: 80%;
      z-index: 5;
      color: #fff;
      text-align: center;
      .line{
        display: block;
        height: 3px;
        width: 200px;
        background-color: #ff6600;
        margin: 0 auto;
        margin-top: 20px;
      }
    }
  }
</style>



