<template lang="pug">
  .home
    nav.header(:class="{headerdown: scrollup}")
      img.logo(:src="logo")
      Dropdown.language(trigger="click",@on-click="languageChange")
        a(href="javascript:void(0)") {{ language == 'CN' ? '中文' : 'English' }}
          Icon(type="arrow-down-b")
        DropdownMenu(slot="list")
          DropdownItem(:selected="language == 'CN'",name="CN") 中文
          DropdownItem(:selected="language == 'EN'",name="EN") English
      .menu
        .menuitem
          a(href="#") {{ $t("content.home") }}
        .menuitem
          Dropdown
            a(href="javascript:void(0)") 关于公司
              Icon(type="arrow-down-b")
            DropdownMenu(slot="list")
              DropdownItem 公司简介
              DropdownItem 团队介绍
              DropdownItem 使命愿景
              DropdownItem 企业文化
        .menuitem
          Dropdown
            a(href="javascript:void(0)") 关于产品
              Icon(type="arrow-down-b")
            DropdownMenu(slot="list")
              DropdownItem 硬件产品
              DropdownItem 软件产品
              DropdownItem 物联网解决方案
        .menuitem
          Dropdown
            a(href="javascript:void(0)") 解决方案
              Icon(type="arrow-down-b")
            DropdownMenu(slot="list")
              DropdownItem 水泵物联网
              DropdownItem 电机物联网
              DropdownItem 工厂物联网
              DropdownItem 数据分析
        .menuitem
          a(href="#") 客户案例
        .menuitem
          Dropdown
            a(href="javascript:void(0)") 技术支持
              Icon(type="arrow-down-b")
            DropdownMenu(slot="list")
              DropdownItem 技术说明
              DropdownItem 下载中心
        .menuitem
          a(href="#") 招贤纳士
        .menuitem
          a(href="#") 联系我们
    Ideas
    Product
    Programme
    Introduce
    Team
    Case
    Partner
    ContactUs
</template>

<script>
  import logo from '../../assets/logo.png'
  import bigimg from '../../assets/video/bigimg.mp4'

  import Ideas from './Ideas'
  import Product from './Product'
  import Programme from './Programme'
  import Introduce from './Introduce'
  import Team from './Team'
  import Case from './Case'
  import Partner from './Partner'
  import ContactUs from './ContactUs'

  export default {
    name: "home",
    data () {
      return {
        language: 'CN',
        logo: logo,
        bigimg: bigimg,
        scrollup: false
      }
    },
    methods: {
      languageChange (val) {
        this.language = val
        this.$i18n.locale = val
      }
    },
    components: {
      Ideas,
      Product,
      Programme,
      Introduce,
      Team,
      Case,
      Partner,
      ContactUs
    },
    mounted () {
      window.onscroll= () => {
        //变量t是滚动条滚动时，距离顶部的距离
        let t = document.documentElement.scrollTop||document.body.scrollTop;
        // console.log(t)
        if (t > 10) {
          this.scrollup = true
          // console.log('改变')
        } else {
          this.scrollup = false
        }
      }
    }
  }
</script>

<style lang="less">
  .home{
    width: 100%;
    height: auto;
    .language{
      position: absolute;
      top: 40px;
      left: 250px;
    }
    .headerdown{
      background-color: #000 !important;
      height: 55px !important;
      .logo{
        margin-top: 0px !important;
      }
      .language{
        top: 20px !important;
      }
      .menu{
        line-height: 55px !important;
        .menuitem{
          height: 55px !important;
          line-height: 55px !important;
        }
      }
    }
    .header{
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 75px;
      border-bottom: 1px solid rgba(255,255,255,0.3);
      background-color: rgba(0,0,0,0);
      z-index: 999;
      transition: all .3s;
      .logo{
        width: 200px;
        height: 50px;
        margin-top: 7.5px;
        margin-left: 15px;
      }
      .menu{
        display: flex;
        position: absolute;
        line-height: 75px;
        top: 0;
        right: 20px;
        .menuitem{
          height: 75px;
          line-height: 75px;
          a{
            display: inline-block;
            width: 100%;
            height: 100%;
            color: #fff;
            font-size: 16px;
            padding: 0 30px;
            &:hover{
              color: #00c9a1;
              cursor: pointer;
            }
          }
        }
      }
    }
  }
</style>



