<template lang="pug">
  .team
    // .mask
    MTitle(:mtitle="$t('content.td')")
    .team-content
      swiper(:options="swiperOption")
        swiper-slide(v-for="(item, index) in team",:key="index")
          .team-item(v-if="index % 2 == 0")
            .team-item-img
              img(:src="item.img")
              .frame.animated.zoomIn
            .team-item-main
              h3 {{ item.name }}
              h4 {{ item.job }}
              p {{ item.content }}
          .team-item(v-else)
            .team-item-main
              h3 {{ item.name }}
              h4 {{ item.job }}
              p {{ item.content }}
            .team-item-img
              img(:src="item.img")
              .frame.animated.zoomIn
    .swiper-button-prev
      Icon(type="chevron-left")
    .swiper-button-next
      Icon(type="chevron-right")
</template>

<script>
  import MTitle from '../../components/MTitle'
  import ysd from '../../assets/img/ysd.jpg'
  import img2 from '../../assets/img/cnzx.png'
  export default {
    name: "team",
    data () {
      return {
        swiperOption: {
          slidesPerView: 4,
          spaceBetween: 0,
          slidesPerGroup: 4,
          pagination: {
            el: '.swiper-pagination'
          },
          navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
          },
          breakpoints: {
            1366: {
              slidesPerView: 3,
              slidesPerGroup: 3
            },
            1024: {
              slidesPerView: 2,
              slidesPerGroup: 2
            },
            768: {
              slidesPerView: 1,
              slidesPerGroup: 1
            },
            640: {
              slidesPerView: 1,
              slidesPerGroup: 1
            }
          }
        },
        team: [
          {img: ysd,name:'杨少东',job:'总经理',content:'美国Cranfield大学，现代智能控制方向博士'},
          {img: img2,name:'杨少东',job:'总经理',content:'美国Cranfield大学，现代智能控制方向博士'},
          {img: ysd,name:'杨少东',job:'总经理',content:'美国Cranfield大学，现代智能控制方向博士'},
          {img: ysd,name:'杨少东',job:'总经理',content:'美国Cranfield大学，现代智能控制方向博士'},
          {img: ysd,name:'杨少东',job:'总经理',content:'美国Cranfield大学，现代智能控制方向博士'}
        ],
        // ysd: ysd
      }
    },
    methods: {
    },
    components: {
      MTitle
    }
  }
</script>

<style lang="less">
  .team{
    height: auto;
    width: 100%;
    text-align: center;
    padding: 50px 20px;
    padding-top: 60px;
    position: relative;
    .mask{
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(0,0,0,0.3);
      z-index: 3;
    }
    .team-content{
      width: 100%;
      margin-top: 60px;
      z-index: 5;
      .team-item{
        width: 100%;
        height: 500px;
        overflow: hidden;
        text-align: left;
        background-color: #fff;
        cursor: pointer;
        transition: all .4s;
        &:hover {
          img,frame{
            transform: scale(0.95, 0.95);
          }
          .frame{
            display: block !important;
          }
         .team-item-main{
           width: 96%;
           margin-left: 2%;
         }
        }
        .team-item-img{
          width: 100%;
          height: 50%;
          overflow: hidden;
          transition: all .4s;
          position: relative;
          img{
            width: 100%;
            height: 100%;
            transition: all .4s;
          }
          .frame{
            position: absolute;
            top: 7%;
            left: 7%;
            border: 1px solid #ddd;
            z-index: 6;
            width: 86%;
            height: 86%;
            display: none;
            -webkit-animation-duration: .3s !important;
            animation-duration: .3s !important;
            -webkit-animation-fill-mode: both !important;
            animation-fill-mode: both !important;
          }
        }
        .team-item-main{
          width: 100%;
          height: 50%;
          transition: all .5s;
        }
        h3, h4, p{
          margin: 0 6%;
          font-weight: 300;
          font-family: HELVETICANEUELTPRO-THEX;
          color: #C0C0C0;
          display: block;
          padding: 0 22px;
          transition: all 0.3s cubic-bezier(0.215,0.61,0.355,1) 0s;
          padding-left: 0;
          font-size: 14px;
        }
        h3{
          padding-top: 20px;
          font-size: 22px;
          color: #000;
          font-weight: 600;
          line-height: 34px;
        }
        h4{
          margin-top: 20px;
          font-size: 18px;
          color: #666;
        }
        p{
          margin-top: 10px;
        }
      }
    }
  }
  .swiper-slide{
    height: 500px;
    background-color: #ddd;
  }
  .swiper-container{
    z-index: 5;
  }
  .swiper-button-prev{
    margin-top: 50px;
    display: inline-block;
    width: 42px;
    border: 1px solid #131313;
    position: relative;
    left: -10px;
    height: 42px;
    cursor: pointer;
    transition: all .6s ease;
    font-size: 26px;
    line-height: 42px;
    font-weight: 300;
    color: #131313;
    background: #fff;
    &:hover{
      background: #00c9a1;
      color: #fff;
      border: 1px solid #00c9a1;
    }
  }
  .swiper-button-next{
    display: inline-block;
    width: 42px;
    border: 1px solid #131313;
    position: relative;
    right: -10px;
    margin-top: 50px;
    height: 42px;
    cursor: pointer;
    transition: all .6s ease;
    font-size: 26px;
    line-height: 42px;
    color: #131313;
    background: #fff;
    &:hover{
      background: #00c9a1;
      color: #fff;
      border: 1px solid #00c9a1;
    }
  }
</style>



